.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes switchColor {
    0% {
        background-color: white;
    }
    50% {
        background-color: red;
    }
    75% {
        background-color: orange;
    }
    100% {
        background-color: green;
    }
}

@keyframes switchColor {
    0% {
        background-color: white;
    }
    50% {
        background-color: red;
    }
    75% {
        background-color: orange;
    }
    100% {
        background-color: green;
    }
}

.popup {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    visibility: hidden;
    opacity: 0;
    display: flex;
    /* justify-content: center; */
    justify-content: space-between;
    align-items: center;
    z-index: 2;
  }
  
  .popup_opened {
    visibility: visible;
    opacity: 1;
  }
  
  .popup__left-wrapper {
    position: relative;
    z-index: 3;
    color: black;
    height: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    /* background-color: #f8f205; */
    background-color: #dfd841;
    /* background-color: #63cbfd; */
    width: 50%;
  }
  
  .popup__left-wrapper[initiateAnimation = '1'] {
    -webkit-animation: switchColor 5s ease-in-out 0s 1 forwards;
            animation: switchColor 5s ease-in-out 0s 1 forwards;
  }

  .popup__left-logo {
    max-width: 65px;
    margin: 0 0 10% 0;
  }
  
  .popup__left-span, .popup__left-p {
    /* font-size: 18px; */
    font-weight: 700;
  }
  
  .popup__left-span {
    font-size: 18px;
  }
  
  .popup__right-wrapper {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50%;
    -webkit-backdrop-filter: blur(2px);
            backdrop-filter: blur(2px);
    position: relative;
    z-index: 3;
  }
  
  .popup__form-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    position: relative;
    z-index: 3;
  }
  
  .popup__close {
    margin: 0 0 0 auto;
    min-height: 30px;
    min-width: 75px;
    background-color: transparent;
    border: 2px solid #f5441e;
    color: #f5441e;
    border-radius: 9px;
    cursor: pointer;
  }
  
  .popup__headline {
    color: #f8f205;
    /* color: #63cbfd; */
  }
  .popup__form {
    display: flex;
    flex-direction: column;
    min-width: 320px;
    min-height: 190px;
    justify-content: space-between;
  }
  
  .popup__form-input {
    min-height: 35px;
    padding: 10px;
    box-sizing: border-box;
    border-radius: 9px;
    border: 2px solid #f8f205;
    background-color: transparent;
    color: #dfd841;
    /* background-color: #e6e1299c; */
  }
  
  .popup__form-input::-webkit-input-placeholder {
    color: white;
  }
  
  .popup__form-input::placeholder {
    color: white;
  }
  
  .popup__form-button {
    /* min-height: 35px; */
    margin: 5% auto 0;
    /* min-width: 120px; */
    min-width: 160px;
    min-height: 55px;
    -webkit-clip-path: polygon(0% 0%, 100% 0%, 100% 80%, 80% 100%, 0% 100%);
            clip-path: polygon(0% 0%, 100% 0%, 100% 80%, 80% 100%, 0% 100%);
    /* border: 2px solid #f8f205; */
    border: 2px solid #f8f205;
    background-color: rgba(0, 0, 0, 0.35);
    /* text-shadow: 0 0 15px #f7f50e; */
    /* color: #f8f205; */
    color: #f8f205;
    position: relative;
    cursor: pointer;
  
  }
  
  .popup__form-button::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    top: 50%;
    left: 80%;
    /* background-color: #f8f205; */
    background-color: #f8f205;
    /* rotate: -25deg; */
    -webkit-transform: rotate(-20deg);
            transform: rotate(-20deg);
  }
  
  .popup__overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.45);
  }
